import React from 'react';
import PropTypes from 'prop-types';
import { Form, Col } from 'react-bootstrap';
import styled from 'styled-components';
import { Button } from '../../../components/button';

const FormFooter = styled.div`
  max-width: 200px;
  text-align: center;
`;

const DarkForm = styled(Form)`
  color: #fff;
`;

export const SubmitButton = styled(Button)`
  color: #fff;
  background-image: ${(props) => (props.primary ? 'var(--secondary)' : 'none')};
  background-color: ${(props) =>
    (props.primary && 'var(--secondary)') ||
    (props.danger && 'red') ||
    (props.warning && 'yellow')};
`;

const ContactForm = ({ className }) => (
  <DarkForm>
    <Form.Row>
      <Form.Group as={Col} controlId="formGridName">
        <Form.Label>Your name</Form.Label>
        <Form.Control type="test" placeholder="Your Name" />
      </Form.Group>

      <Form.Group as={Col} controlId="formGridEmail">
        <Form.Label>Email</Form.Label>
        <Form.Control type="email" placeholder="Enter email" />
      </Form.Group>
    </Form.Row>

    <Form.Group controlId="formGridMessage">
      <Form.Label>Message</Form.Label>
      <Form.Control as="textarea" placeholder="Message" />
    </Form.Group>

    <FormFooter className="mx-auto">
      <SubmitButton primary type="submit">
        Submit
      </SubmitButton>
    </FormFooter>
  </DarkForm>
);
ContactForm.defaultProps = {
  className: ''
};
ContactForm.propTypes = {
  className: PropTypes.string
};
export default ContactForm;
